import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import {useSpring, animated}  from 'react-spring';

function Omeni(props) {

  const fade = useSpring({
    from: {opacity: 0, transform: 'scale(0.5)'},
    to: {opacity: 1, transform: 'scale(1)'},
    config: {
      duration: 1000,
      precision: 0.05,
      clamp: true
    }
  });

  return (
    <animated.div className="omeni" style={fade}>   
      <h2 className="mt-5">Nikola Furundzic</h2>
      <h5>Fotograf / Videograf </h5>
      <img src={'./icons/profilna.jpg'} className="profilna mt-5" alt="profilna"/>
      <div className="mt-5 omeni-text">
        <span>Nikola je fotograf/videograf iz Kraljeva, sa preko 10 godina iskustva na tim poljima. </span>
        <span>Uza specijalnost su mu fotografisanje proizvoda kao i izrada korporativnih video snimaka. </span>
        <span>Pored toga bavi se i fotografisanjem vencanja, rodjendana kao i svih vrsta proslava. </span>
      </div>
      <span> </span>
      <span style={{fontWeight: 'bold'}}> Saradnje:   </span>
    <div style={{flexDirection: "column"}} className="slike">
       <img src={'./firme/pekom.png'} className="mt-5 mx-2 slike-saradnja" alt="pekom"/>
       <img src={'./firme/banim.png'} className="mt-5 mx-2 slike-saradnja" alt="banim"/>
       <img src={'./firme/interpak.png'} className="mt-5 mx-2 slike-saradnja" alt="interpak"/>
       <img src={'./firme/krsenkovic.jpg'} className="mt-5 mx-2 slike-saradnja" alt="krsenkovic"/>
       <img src={'./firme/gruzanska.jpg'} className="mt-5 mx-2 slike-saradnja gruzanska" alt="gruzanska"/>
       <img src={'./firme/aloba.jpg'} className="mt-5 mx-2 slike-saradnja" alt="aloba"/>
    </div>
    </animated.div>
  );
}

export default Omeni;