import React, { useState, useCallback, useEffect } from "react";
import { render } from "react-dom";
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";
import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import {useSpring, animated}  from 'react-spring';



function Galerija(props) {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const fade = useSpring({
    opacity: props.loading ? 1 : 0,
    transform: props.loading ? 'scale(1)' : 'scale(0.5)',
    config: {
      duration: 1000,
      precision: 0.05,
      clamp: true
    }
  });

    const openLightbox = useCallback((event, { photo, index }) => {
      setCurrentImage(index);
      setViewerIsOpen(true);
    }, []);
  
    const closeLightbox = () => {
      setCurrentImage(0);
      setViewerIsOpen(false);
    };
   
    const filterImages = (e) => {
      if(!e) return;
      props.filterImages(e);
    }

  return (
    <div className="mt-5">
     {props.filterLoading === true && <animated.div style={fade} className="mt-2 galerija" onClick={(e) => filterImages(e.target.id)}>
        <a href="#" className="menu"><div id="wedding">Vencanja  </div></a>
        <a href="#" className="menu"><div id="christening">Krstenja  </div></a>
        <a href="#" className="menu"><div id="birthday">Punoletstva  </div></a>
        <a href="#" className="menu"><div id="products">Produkti</div></a>
      </animated.div>  } 
      {props.loading === true && <animated.div style={fade}>
      <span className="mt-2"> </span>
      <Gallery photos={props.photos} onClick={openLightbox} />
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={props.photos.map(x => ({
                ...x,
                srcset: x.srcSet,
                caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>

      <span className="mb-2"> </span> 
      </animated.div>
      }
    </div>
  );
}

export default Galerija;