import React, {useState} from 'react';
import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import {useSpring, animated}  from 'react-spring';


function Kontakt() {

  const [ime, setIme] = useState('');
  const [poruka, setPoruka] = useState('');
  const [email, setEmail] = useState('');

  const fade = useSpring({
    from: {opacity: 0, transform: 'scale(0.5)'},
    to: {opacity: 1, transform: 'scale(1)'},
    config: {
      duration: 1000,
      precision: 0.05,
      clamp: true
    }
  });

  const posaljiPoruku = (e) => {
    fetch('/api/sendEmail', {
      method: "POST",
      headers: {
        "Content-Type" : "application/json"
      },
      body: JSON.stringify({
        ime,
        email,
        poruka
      })
    });
  };


  return (
 
    <animated.div className="kontakt mx-4" style={fade}> 
          <span className="mt-5"> </span>   
      <span>Ukoliko imate bilo kakvih pitanja mozete me kontaktirati: </span>
      <span> </span>
      <span style={{color: "red"}}>evolutionphotokv@gmail.com </span>
      <span style={{color: "red"}}>Telefon: 060/375-0-661</span>
      <span> </span>
      <span >Ili putem kontakt forme</span>
      <span> </span>
    <div className="kontaktForma">
      <input className="form-control kontaktIme no-border" type="text" id="textIme" placeholder="Ime" onChange={(e)=> setIme(e.target.value)}/>
      <span> </span>
      <input className="form-control kontaktIme no-border" type="text" id="textEmail" placeholder="Email" onChange={(e)=> setEmail(e.target.value)}/>
      <span> </span>
      <textarea className="form-control no-border" id="textPoruka" rows="5" placeholder="Vasa poruka" onChange={(e)=> setPoruka(e.target.value)}/>
      <span> </span>
      <button type="button" class="btn btn-danger mt-3 no-border" onClick={posaljiPoruku}>Posalji</button>
    </div>
    </animated.div>

  );
}

export default Kontakt;