import React, {useState} from 'react';
import './App.css';
import  { Button, Navbar, Container, Nav, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'

function Navigacija(props) {



  return (  

      <div className="row " onClick={(e)=> props.promeniMeni(e.target.id)}>
    <div className="col-2" > 
        <img src={'./mainLogo.png'} alt="logo" width="200px"  />
    </div>
    <div className="col-5">
    </div>
    <div className="col-5 pt-4">
      <a className="menu" id="omeni" href="#"> O Meni      </a>
      <a className="menu" id="vencanja" href="#">Fotografije      </a>
      <a className="menu" id="video" href="#">Video        </a>
      <a className="menu" id="kontakt" href="#">Kontakt      </a>
     
      <span>         </span> <a href="https://instagram.com/fotofuki" target="_blank"><img src={'./icons/instagram.png'} alt="instagram" width="16px" className="socialIcons"/></a> <span>   </span>
      <a href="https://facebook.com/fotofuki" target="_blank"><img src={'./icons/facebook.png'} alt="facebook" width="10px" className="socialIcons"/></a>  <span>   </span>
      <a href="https://youtube.com" target="_blank"><img src={'./icons/youtube.png'} alt="youtube" width="20px" className="socialIcons"/></a>
      
   

    </div>
  </div>
   
  );
}

export default Navigacija;