import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import Omeni from './Omeni';
import Kontakt from './Kontakt';
import Video from './Video';
import Galerija from './Galerija';
import Navigacija from './Navigacija';
import LoadingScreen from './LoadingScreen';
import Footer from './Footer';
import { serverUrl } from './Database';


function App() {

  const [oMeniTF, setOmeniTF] = useState(false);
  const [vencanja, setVencanja] = useState(true);
  const [kontakt, setKontakt] = useState(false);
  const [video, setVideo] = useState(false);
  let imagesShallow = [];
  let videosShallow = [];

  const promeniMeni = async (e) => {
    if(!e) return;

    if(e === "omeni") {
      setOmeniTF(true); setVencanja(false); setKontakt(false); setVideo(false);
    }
    else if (e === "vencanja") {
      setOmeniTF(false); setVencanja(true); setKontakt(false); setVideo(false);
    }
    else if (e === "video") {
      // setLoading(false);
      setVideos([]);
      const vid = await getVideos();
      if(!vid) return;
       for(let video of Object.values(vid)){
          const newVideo = {
            src: video.src,
            type: video.type
          };
          videosShallow.push(newVideo);
        };
        
      setVideos(videosShallow);
      setOmeniTF(false); setVencanja(false); setKontakt(false); setVideo(true);
      // setLoading(true);
    }
    else if (e === "kontakt") {
      setOmeniTF(false); setVencanja(false); setKontakt(true); setVideo(false);
    }
  }

  const getVideos = async () => {
    const res = await fetch(`${serverUrl}/videos.json`);
    const data = await res.json();
    return data;
  };
  
  const [videos, setVideos] = useState([]);
  const [photos, setPhotos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [filterLoading, setFilterLoading] = useState(false);

  const getImages = async () => {
    const res = await fetch(`${serverUrl}/images.json`);
    const data = await res.json();
    return data;
  }

  useEffect( async () => {
    imagesShallow = [];
    setLoading(false);
    const images = await getImages();
    if(!images) {
      setLoading(true);
      return;
    };
    for (let img of Object.values(images)) {
      const newLink = {
            src: img.src,
            width: img.width,
            height: img.height,
            class: "images",
            type: img.type          
          };
          imagesShallow.push(newLink);
    };
    shuffleArray(imagesShallow);
    setPhotos(imagesShallow);
    setLoading(true);
    setFilterLoading(true);
  }, []);
  
    const shuffleArray = (array) => {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      };
    };

    const filterImages = async (e) => {
    imagesShallow = [];
    setLoading(false);
    setPhotos([]);
    const images = await getImages();
    for (let img of Object.values(images)) {
      if(img.type === e) {
      const newLink = {
            src: img.src,
            width: img.width,
            height: img.height,
            class: "images",
            type: img.type            
          };
        imagesShallow.push(newLink);
      };
    };
    setPhotos(imagesShallow);
    setLoading(true);
  };

  const filterVideos = async (e) => {
    setLoading(false);
    videosShallow = [];
    setVideos([]);
    const videos = await getVideos();
    for (let vid of Object.values(videos)) {
      if(vid.type === e) {
        const newVideo = {
          src: vid.src,
          type: vid.type
        };
        videosShallow.push(newVideo);
      };
    };
    setVideos(videosShallow);
    setLoading(true);
  };

  const [test, setTest] = useState('');
  return (
    <div className="container">

      <button onClick={async () =>{
        const res = await fetch('/test');
        console.log(res);
        const data = await res.json();
        setTest(data);

      }}>TEST</button>
      {test}

      <Navigacija promeniMeni={promeniMeni}/>
      {oMeniTF === true && <Omeni />}
      {kontakt === true &&  <Kontakt />}
      {video === true && <Video videos={videos} filterVideos={filterVideos} loading={loading}/>}
      {vencanja === true && <Galerija photos={photos} loading={loading} filterImages={filterImages} filterLoading={filterLoading}/>}   
      {loading === false && <LoadingScreen /> }
     <center className="mb-3 mt-1"> <Footer /> </center>
    </div>
  );
}

export default App;
