import React, {useEffect, useState} from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css'
import {useSpring, animated}  from 'react-spring';



function Video(props) {

  const fade = useSpring({
    opacity: props.loading ? 1 : 0,
    transform: props.loading ? 'scale(1)' : 'scale(0.5)',
    config: {
      duration: 1000,
      precision: 0.05,
      clamp: true
    }
  });

  const filterVideos = (e) => {
    if(!e) return;
    props.filterVideos(e);
  }

    return (
      <div>    
      <animated.div style={fade} className="mt-2 galerija"  onClick={(e) => filterVideos(e.target.id)}>
      <a href="#" className="menu menuVideo"><div id="christening">Krstenja  </div></a>
      <a href="#" className="menu menuVideo"><div id="creative">Kreativni  </div></a>
      <a href="#" className="menu menuVideo"><div id="corporative">Korporativni  </div></a>
      <a href="#" className="menu menuVideo"><div id="birthday">Rodjendani  </div></a>
    </animated.div> 
    <animated.div className="mt-5" style={fade}> 
      {Object.values(props.videos).map((x, id) =>
      <div className="m-1 videos" key={id += 1}>
        <iframe width="325" height="200" src={"https://www.youtube.com/embed/" + x.src} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>  
        </div>
        )} 
    </animated.div>

    </div>
  );
}

export default Video;